.timeline-guid {
  margin: 25px auto;
  position: fixed;
  top: 60px;
  width: 100px;
  text-align: center;
  right: 15px;
  background: white;
  z-index: 999;
  button {
    margin-bottom: 15px;
    a {
      position: relative;
      z-index: 999;
      display: block;
      color: #000;
    }
  }
}